	module.exports = {"data":{"post":{"html":"<p>Mama's PC is getting a long-needed software update, a nice fresh Windows 2000 Pro. Yes, anyone named <acronym title=\"she who must not be named\">Voldemort</acronym> must be using Windows 2000. We finished the chicken-and-egg driver phase (i.e. you need a network driver to connect to the Internet, but to get the driver you need to be on the Internet) and the 30MB-of-security-updates phase. We still need to find the printer and audio drivers, install her aps, and make the desktop pretty.</p>\n<p>I'd love to give her Linux, but her job as Voldemort requires that she work with tools of the devil like Word documents and Excel sheets. And she doesn't feel like learning a 2nd OS, and I don't blame her.</p>","fields":{"title":"Desktop of Evil","date":"17 Jun 2003","url":"https://papascott.de/2003-06-17-desktop-of-evil/"}}},"pathContext":{"slug":"/2003-06-17-desktop-of-evil/","prev":{"fields":{"title":"50th anniversary of GDR uprising","date":"17 Jun 2003","slug":"/2003-06-17-50th-anniversary-of-gdr-uprising/"}},"next":{"fields":{"title":"East Berlin June 17, 1953: Stones Against Tanks","date":"17 Jun 2003","slug":"/2003-06-17-east-berlin-june-17-1953-stones-against-tanks/"}}}}

